@font-face {
    font-family: 'SuperSmash';
    src: local('SuperSmash'), url('./fonts/super_smash.ttf') format('truetype');
}

html {
    font-family: 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

body {
    margin: 0;
    font-family: 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    background-image: url('otherPictures/ssbu_landscape.jpg');
    background-size: cover;
}

.bobeffect {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
}

.bobeffect:hover, .bobeffect:focus, .bobeffect:active {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
    transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
}


.resetbtn {
    height: 50px;
    width: 100px;
    font-size: 20px;
    margin: 100px;

}

.MuiButtonBase-root:disabled {
  cursor: not-allowed;
  pointer-events: auto;
}

.iconsize{
    height: 100px;
    width: 100px;
    margin: 10px;
}

.bigTitle{
    font-size: 50px;
    margin:auto;
    font-family: 'Roboto', serif;
}

.customTitle{
    text-align: center;
    font-family: 'SuperSmash', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', serif;
}

